module.exports = "Main goal of typography in data visualization should be describe information and facilitate users to focus in the underlying data. Typography done right helps present the information in the most efficient and direct way.\n\n# Table of contents\n- [Style](#style)\n  * [Weights](#weights)\n  * [Italic](#italic)\n  * [Type color](#type-color)\n- [Uses](#uses)\n  * [Body](#body)\n  * [Headings](#headings)\n\nWe use the open-source typeface [IBM Plex](https://github.com/IBM/plex) and its \"sans\", \"serif\" and \"mono\" families to carry out this task.\n\n```type\n{\n  \"headings\": [\n    { \"label\": \"IBM Plex Sans\", \n    \"value\": 54 },\n  ],\n  \"font\": \"IBM Plex Sans\"\n}\n```\n```type\n{\n  \"headings\": [\n    { \"label\": \"IBM Plex Serif\", \n    \"value\": 54 },\n  ],\n  \"font\": \"IBM Plex Serif\"\n}\n```\n```type\n{\n  \"headings\": [\n    { \"label\": \"IBM Plex Mono\", \n    \"value\": 54 },\n  ],\n  \"font\": \"IBM Plex Mono\"\n}\n```\n\n### Sans-serif font stack\n\n```\nfont-family: \"IBM Plex Sans\", \"Helvetica Neue\", Arial, sans-serif;\n```\n### Serif font stack\n\n```\nfont-family: \"IBM Plex Serif\", \"Georgia\", Times, serif;\n```\n### Mono font stack\n\n```\nfont-family: \"IBM Plex Mono\", \"Menlo\", \"DejaVu Sans Mono\",\n  \"Bitstream Vera Sans Mono\", Courier, monospace;\n```\n\n[to the top](#table-of-contents)\n\n# Style\nTypography guides users to read and understand the hierarchy of information. The right typographic treatment and the controlled usage of type styles helps manage the display of content, keeping it useful, simple, and effective.\n\n## Weights\nFont weight is an important typographic variable that can add emphasis and differentiate content hierarchy. We use IBM Plex Light, Regular, and SemiBold to display data information. The semibold weight is ideal for chart headers, but should not be used for long text.\n\n```type\n{\n  \"headings\": [\n    { \"label\": \"IBM Plex Sans Semibold\", \n    \"value\": 54 },\n  ],\n  \"font\": \"IBM Plex Sans Semibold\",\n  \"weight\": 600\n}\n```\n```type\n{\n  \"headings\": [\n    { \"label\": \"IBM Plex Sans\", \n    \"value\": 54 },\n  ],\n  \"font\": \"IBM Plex Sans\",\n  \"weight\": 400\n}\n```\n```type\n{\n  \"headings\": [\n    { \"label\": \"IBM Plex Sans Light\", \n    \"value\": 54 },\n  ],\n  \"font\": \"IBM Plex Sans Light\",\n  \"weight\": 300\n}\n\n```\n## Italic\nEach weight has an italic style, which should only be used when you need to emphasize certain words in a sentence (titles of works, technical terms, names of devices, captions, etc.).\n\n```type\n{\n  \"headings\": [\n    { \"label\": \"IBM Plex Sans Semibold\", \n    \"value\": 54 },\n  ],\n  \"font\": \"IBM Plex Sans Semibold\",\n  \"weight\": 600,\n  \"style\": italic\n}\n```\n```type\n{\n  \"headings\": [\n    { \"label\": \"IBM Plex Sans\", \n    \"value\": 54 },\n  ],\n  \"font\": \"IBM Plex Sans\",\n  \"weight\": 400,\n  \"style\": italic\n}\n```\n```type\n{\n  \"headings\": [\n    { \"label\": \"IBM Plex Sans Light\", \n    \"value\": 54 },\n  ],\n  \"font\": \"IBM Plex Sans Light\",\n  \"weight\": 300,\n  \"style\": italic\n}\n```\n\n## Type color\nKeep type color neutral avoiding color whenever is possible. Use q-pri-color-one-1 if something needs to be hightlighted.\n\n```type|span-3,kern,smoothen,single\n{\n    \"headings\": [\n      {\"label\": \"Color neutral\", \n      \"value\": 54 },\n    ],\n    \"background\": \"#ffffff\",\n    \"color\": \"#05032d\",\n    \"font\": \"IBM Plex Sans\",\n    \"weight\": 600\n}\n```\n\n```type|span-3,kern,smoothen,single\n{\n    \"headings\": [\n      {\"label\": \"Color neutral background\", \n      \"value\": 54 },\n    ],\n    \"background\": \"#05032d\",\n    \"color\": \"#ffffff\",\n    \"font\": \"IBM Plex Sans\",\n    \"weight\": 600\n}\n```\n\n```type|span-3,kern,smoothen,single\n{\n    \"headings\": [\n      {\"label\": \"Color neutral\", \n      \"value\": 54 },\n    ],\n    \"background\": \"#ffffff\",\n    \"color\": \"#22577a\",\n    \"font\": \"IBM Plex Sans\",\n    \"weight\": 600\n}\n```\n\n```type|span-3,kern,smoothen,single\n{\n    \"headings\": [\n      {\"label\": \"Color neutral background\", \n      \"value\": 54 },\n    ],\n    \"background\": \"#22577a\",\n    \"color\": \"#ffffff\",\n    \"font\": \"IBM Plex Sans\",\n    \"weight\": 600\n}\n```\n\n[to the top](#table-of-contents)\n\n# Uses\n## Body\n```type\n{\n  \"paragraphs\": [\n    {\n      \"label\": \"For short paragraphs\",\n      \"value\": 14/16 }\n      ],\n  \"font\": \"IBM Plex Sans\",\n  \"color\": \"#05032d\",\n  \"label\": \"For short paragraphs\"\n}\n```\n\n```type\n{\n  \"paragraphs\": [\n    {\n      \"label\": \"For long paragraphs\",\n      \"value\": 14/20 }\n      ],\n  \"font\": \"IBM Plex Sans\",\n  \"color\": \"#05032d\",\n  \"label\": \"For short paragraphs\"\n}\n```\n\n```type\n{\n  \"paragraphs\": [\n    {\n      \"label\": \"For long paragraphs no more than four lines\",\n      \"value\": 16/22 }\n      ],\n  \"font\": \"IBM Plex Sans\",\n  \"color\": \"#05032d\",\n  \"label\": \"For short paragraphs\"\n}\n```\n\n```type\n{\n  \"paragraphs\": [\n    {\n      \"label\": \"For long paragraphs with more than four lines\",\n      \"value\": 16/24 }\n      ],\n  \"font\": \"IBM Plex Sans\",\n  \"color\": \"#05032d\",\n  \"label\": \"For short paragraphs\"\n}\n```\n\n[to the top](#table-of-contents)\n\n## Headings\n```type\n{\n  \"headings\": [54, 42, 32, 28, 20, 16, 14],\n  \"font\": \"IBM Plex Sans\",\n  \"color\": \"#05032d\"\n}\n```\n\n[to the top](#table-of-contents)"

